@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.App {
  overflow: hidden;
  max-width: 1024px;
  margin: 0 auto;
  background-color: beige;
  color: white;
  width: 100%;
  padding: 32px 24px;
  min-height: 100vh;
  font-family: 'Roboto', 'Open-Sans', sans-serif;
  background: linear-gradient(to bottom, rgb(43, 50, 178) 0%, rgb(20, 136, 204) 80%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.App > * {
  width: 100%;
}

.display-no-city {
  font-size: 2rem;
}
.display-no-city strong {
  text-transform: capitalize;
}

@media screen and (min-width: 480px) and (orientation: portrait) {
  .App {
    padding: 40px 32px;
  }
}

.form-input {
  width: 100%;
  height: 48px;
  font-size: 1rem;
  color: #1c4497;
  /* font-weight: bold; */
  border-radius: 8px;
  margin-bottom: 24px;
  padding: 8px 16px;
  letter-spacing: 1px;
  border: none;
  outline: none;
}

@media screen and (min-width: 480px) and (orientation: portrait) {
  .form-input {
    height: 54px;
    font-size: 1.3rem;
  }
}

/* .location {
  width: 100%;
} */
.location > * {
  width: 100%;
}
.location--city-country {
  margin-bottom: 8px;
  font-size: 1.8rem;
  display: inline-block;
  text-transform: capitalize;
  font-weight: bold;
}
.location--date {
  font-size: 1rem;
}
@media screen and (min-width: 480px) {
  .location--date {
    font-size: 1.3rem;
  }
}

@media screen and (min-width: 768px) {
  .location--city-country {
    font-size: 2.6rem;
  }
}

.current-temp {
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  margin-top: 32px;
}
.current-temp--icon {
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
}

.current-temp--temp {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
  text-align: left;
  font-size: 4.8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
}

.current-temp--temp > * {
  width: 100%;
}
.current-temp--icon-img {
  width: 110%;
  margin: -10%;
}
.current-temp--deg {
  margin: -10% 0;
}
.current-temp--desc {
  font-size: 1.1rem;
}

@media screen and (orientation: landscape) {
  .current-temp--icon-img {
    width: 85%;
    margin: -15% -5%;
  }
  .current-temp--temp {
    font-size: 5.4rem;
  }
  .current-temp--deg {
    margin: 0;
  }
}

@media screen and (min-width: 375px) and (orientation: portrait) {
  .current-temp--deg {
    margin: -5% 0;
  }
  .current-temp--icon-img {
    width: 100%;
    margin: -20% -5%;
  }
}

@media screen and (min-width: 414px) and (orientation: portrait) {
  .current-temp--icon-img {
    width: 80%;
  }
  .current-temp--desc {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 480px) and (orientation: portrait) {
  .current-temp--temp {
    font-size: 5rem;
  }
  .current-temp--desc {
    font-size: 1.4rem;
  }
}

@media screen and (min-width: 568px) and (orientation: portrait) {
  .current-temp {
    width: 50%;
  }
  .current-temp--icon {
    -webkit-flex-basis: 55%;
            flex-basis: 55%;
  }
  .current-temp--temp {
    -webkit-flex-basis: 45%;
            flex-basis: 45%;
    font-size: 4rem;
    margin: -10% 0 0;
  }

  .current-temp--icon-img {
    width: 100%;
    margin: -15% -10% -10%;
  }
  .current-temp--desc {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 568px) and (orientation: landscape) {
  .current-temp {
    width: 50%;
    margin-top: 24px;
  }
  .current-temp--icon {
    -webkit-flex-basis: 55%;
            flex-basis: 55%;
  }
  .current-temp--temp {
    -webkit-flex-basis: 45%;
            flex-basis: 45%;
    font-size: 4rem;
  }
  /* .current-temp--deg {
    margin: -40% 0 0;
  } */

  .current-temp--icon-img {
    width: 110%;
    margin: -20% -10%;
  }
  .current-temp--desc {
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 768px) and (orientation: landscape) {
  .current-temp--icon-img {
    width: 90%;
    margin: -20% -5% 0;
  }
  .current-temp--deg {
    margin: -20% 0 0;
  }
  .current-temp--temp {
    font-size: 5.4rem;
  }
  .current-temp--desc {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 1024px) and (orientation: landscape) {
  .current-temp--icon-img {
    width: 85%;
    margin: -20% 0;
  }
  .current-temp--deg {
    margin: -10% 0 0;
  }
}

.current-stats {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding-bottom: 24px;
}

.current-stats--last-col {
  padding-left: 11%;
}

.current-stats--value {
  font-size: 1.4rem;
  margin-top: 24px;
}
.current-stats--value span {
  font-size: 1.3rem;
}
.current-stats--desc {
  color: rgba(255, 255, 255, 0.6);
}

@media screen and (min-width: 375px) and (orientation: portrait) {
  .current-stats {
    margin-top: 16px;
  }
}

@media screen and (min-width: 568px) and (orientation: landscape) {
  .current-stats {
    width: 50%;
    border-top: none;
    border-bottom: none;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
  }
  .current-stats--value {
    font-size: 1.8rem;
  }
  .current-stats--value span {
    font-size: 1.6rem;
  }
  .current-stats--desc {
    font-size: 1.4rem;
  }
}

@media screen and (min-width: 568px) {
  .current-stats {
    width: 50%;
    border-top: none;
    border-bottom: none;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    padding-bottom: 0;
  }
  .current-stats--value {
    font-size: 1.4rem;
  }
  .current-stats--value span {
    font-size: 1.3rem;
  }
  .current-stats--desc {
    font-size: 1.1rem;
  }
}

/* @media screen and (min-width: 1024px) and (orientation: landscape) {
} */

.next-days h2 {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  font-weight: normal;
  margin: 24px 0 16px;
}
.next-days--day {
  background-color: rgba(0, 0, 0, 0.2);
  /* background-color: rgba(100, 100, 100, 0.3); */
  border-radius: 5px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 16px 0;
  margin: 10px 0;
}

.next-days--tile {
  -webkit-flex-basis: 33.33%;
          flex-basis: 33.33%;
  margin: 8px 0;
  text-align: center;
}

.next-days--tile {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.next-days--value {
  font-size: 1.1rem;
}
.next-days--desc {
  color: rgba(255, 255, 255, 0.6);
}

.next-days--icon {
  width: 100%;
  margin: -20% -20% -35%;
}

/* ------ MEDIA ------ */

@media screen and (orientation: landscape) {
  .next-days--tile {
    -webkit-flex-basis: 16.66%;
            flex-basis: 16.66%;
  }
  .next-days--tile.forecast-icon {
    -webkit-order: -1;
            order: -1;
  }
  .next-days--tile.date {
    -webkit-order: -2;
            order: -2;
  }
}

@media screen and (min-width: 375px) and (orientation: portrait) {
  .next-days h2,
  .next-days--value {
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 480px) and (orientation: portrait) {
  .next-days h2,
  .next-days--value {
    font-size: 1.3rem;
  }
  .next-days--desc {
    font-size: 1rem;
  }
  .next-days--icon {
    width: 75%;
    margin: -20% 0;
  }
}

@media screen and (min-width: 568px) and (orientation: portrait) {
  .next-days--icon {
    width: 60%;
  }
}
@media screen and (min-width: 600px) and (orientation: landscape) {
  .next-days h2,
  .next-days--value {
    font-size: 1.3rem;
  }
  .next-days--desc {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 768px) and (orientation: portrait) {
  .next-days--tile {
    -webkit-flex-basis: 16.66%;
            flex-basis: 16.66%;
  }
  .next-days--tile.forecast-icon {
    -webkit-order: -1;
            order: -1;
  }
  .next-days--tile.date {
    -webkit-order: -2;
            order: -2;
  }
}

@media screen and (min-width: 768px) and (orientation: landscape) {
  .next-days--icon {
    width: 80%;
    margin: -20% -20% -30%;
  }
}

